import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'app-organization',
    templateUrl: './organization.component.html'
})
export class OrganizationComponent implements OnInit {
    itemsPhoneArray: any = [];
    itemsMobileArray: any = [];
    itemsEmailArray: any = [];
    taxItemsArray: any = [];

    constructor() { }

    ngOnInit(): void {
        this.addPhoneItem();
        this.addMobileItem();
        this.addEmailItem();
        this.addTaxItem();
    }

    trackByFn(item: any, id: any) {
        return item;
    }

    addPhoneItem() {
        this.itemsPhoneArray.push("");
    }

    removePhoneItem(index: any) {
        this.itemsPhoneArray.splice(index, 1);;
    }

    addMobileItem() {
        this.itemsMobileArray.push("");
    }

    removeMobileItem(index: any) {
        this.itemsMobileArray.splice(index, 1);;
    }

    addEmailItem() {
        this.itemsEmailArray.push("");
    }

    removeEmailItem(index: any) {
        this.itemsEmailArray.splice(index, 1);;
    }

    addTaxItem() {
        let obj = {};
        this.taxItemsArray.push(obj);
    }

    removeTaxItem(index: any) {
        this.taxItemsArray.splice(index, 1);
    }
}